@import "../../global.scss";

.work{
    background-color: lightgreen;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    
    .arrow{
        height: 50px;
        position: absolute;

        @include mobile{
            display: none;
        }

        &.left{
            left: 100px;
            transform: rotateY(180deg);
            cursor: pointer;
        }

        &.right{
            right: 100px;
            cursor: pointer;
        }
    }

    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease;

        @include mobile{
            height: 100vh;
            flex-direction: column;
            justify-content: center;
        }

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include mobile{
                    width: 80%;
                    height: 150px;
                    margin: 15px 0px;
                }

                .left{
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        @include mobile {
                            width: 100%;
                            height: 100%;
                        }

                        .imgContainer{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: coral;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            @include mobile {
                                width: 20px;
                                height: 20px;
                                margin-left: 5px;
                            }

                            img{
                                width: 25px;

                                @include mobile {
                                    width: 15px;
                                }
                            }
                        }

                        h2{
                            font-size: 20px;

                            @include mobile {
                                margin-left: 5px;
                                font-size: 13px;

                            }
                        }

                        p{
                            font-size: 13px;

                            @include mobile {
                                display: none;
                            }
                        }

                        // span{
                        //     font-size: 12px;
                        //     font-weight: 600;
                        //     text-decoration: underline;
                        //     cursor: pointer;

                        //     @include mobile{
                        //         margin-left: 5px;
                        //     }
                        // }
                    }

                }

                .right{
                    flex: 8;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;

                    img{
                        width: 400px;
                        transform: rotate(-10deg);
                    }
                    
                }
            }
        }
    }
}